<template>
  <div class="search">
    <van-nav-bar fixed title="商品列表" left-arrow @click-left="$router.go(-1)" />

    <van-search
      readonly
      shape="round"
      background="#ffffff"
      :value="querySearch || '搜索商品' "
      show-action
      @click="$router.push('/search')"
    >
      <template #action>
        <van-icon class="tool" name="apps-o" />
      </template>
    </van-search>

    <!-- 排序选项按钮 -->
    <div class="sort-btns">
      <div class="sort-item" :class="{active: sortType === 'all'}" @click="sortAll">综合</div>
      <div class="sort-item" :class="{active: sortType === 'sales'}" @click="sortSales">销量</div>
      <div class="sort-item" :class="{active: sortType === 'price'}" @click="sortPrices">
        价格
        <van-icon :name="sortPrice === '0' ? 'arrow-up' : 'arrow-down'" />
      </div>
    </div>

    <div class="goods-list">
      <GoodsItem v-for="item in proList" :key="item.goods_id" :item="item"></GoodsItem>
    </div>
  </div>
</template>

<script>
import GoodsItem from '@/components/GoodsItem.vue'
import { getProList } from '@/api/product'
export default {
  name: 'SearchList',
  data () {
    return {
      page: 1,
      proList: [],
      sortType: 'all', // 默认综合排序 sales:销量 price:价格
      sortPrice: '0' // 0-低到高，1-高到低
    }
  },

  created () {
    this.getProList()
  },

  components: {
    GoodsItem
  },

  computed: {
    querySearch () {
      return this.$route.query.search
    }
  },

  methods: {
    async getProList () {
      const { data: { list } } = await getProList({
        sortType: this.sortType,
        sortPrice: this.sortPrice,
        categoryId: this.$route.query.categoryId,
        goodsName: this.querySearch,
        page: this.page
      })
      this.proList = list.data
    },
    sortAll () {
      if (this.sortType !== 'all') {
        this.sortType = 'all'
        this.getProList()
      }
    },
    sortSales () {
      if (this.sortType !== 'sales') {
        this.sortType = 'sales'
        this.getProList()
      }
    },
    sortPrices () {
      this.sortType = 'price'
      this.sortPrice = this.sortPrice === '0' ? '1' : '0'
      this.getProList()
    }

  }
}
</script>

<style lang="less" scoped>
.search {
  padding-top: 46px;
  ::v-deep .van-icon-arrow-left {
    color: #333;
  }
  .tool {
    font-size: 24px;
    height: 40px;
    line-height: 40px;
  }

  .sort-btns {
    display: flex;
    height: 36px;
    line-height: 36px;
    .sort-item {
      text-align: center;
      flex: 1;
      font-size: 16px;
    }
    .active {
      color: #ee0a24;
      font-weight: bold;
    }
  }
}

// 商品样式
.goods-list {
  background-color: #f6f6f6;
}
</style>
